import React from "react";

export default function Display(props) {
  let { mins, secs, styleColor, curMod } = props;
  if (Number(mins) < 10) mins = `0${mins}`;
  if (Number(secs) < 10) secs = `0${secs}`;

  return (
    <div style={styleColor} className="display">
      <div className="display__mode">
        <span id="timer-label" className="display__mode__title">
          {curMod}
        </span>
      </div>
      <div className="display__counter">
        <span
          id="time-left"
          className="display__counter__counter"
        >{`${mins}:${secs}`}</span>
      </div>
    </div>
  );
}
